import React from 'react';

import { InputGroup, Input, InputGroupAddon, Button, FormGroup, Label } from 'reactstrap'
import Header from './ui/Header';
import { ThemeProvider } from '@material-ui/core/styles';
import theme from './ui/Theme'
import RecipeReviewCard from './ui/Cards';
import MediaControlCard from './ui/CommentCards';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import MainLayout from './ui/MainLayout';
import PictureCard from './ui/PictureCard';


function App() {  
   return (
      <>
        <Router>
         <ThemeProvider theme={theme} >
           <MainLayout>
              <Switch>
           <Route path={'/posts'} exact component={RecipeReviewCard} />
           <Route path={'/'} exact component={RecipeReviewCard} />
           <Route path={'/comments'} exact component={MediaControlCard} />
           <Route path={'/pictures'} exact component={PictureCard} />
           </Switch>
           </MainLayout>
         </ThemeProvider>
         </Router>

      </>
   )

}

export default App;
